.body {
  padding: 40px;
  padding-bottom: 30%;
  text-align: center;
  height: 100vh;
}

.body h1 {
  text-align: center;
  color: #000000;
  margin: 40px 0px;
}

.button {
  margin: 20px 0px;
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.showmore {
  list-style: none;
  padding-top: 20px;
  background-color: #ffe6e6;
  width: 350px;
  border-radius: 10px;
  box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  -webkit-box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  -moz-box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  margin: 10px auto;
  padding: 20px;
}

.cardholder {
  animation: leftToRight 3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

@keyframes leftToRight {
  0% {
    transform: translateY(-70%);
    opacity: 0;
  }

  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
