@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto:wght@500&display=swap");

.adjust {
  margin-left: auto;
}

.open {
  text-align: center;
  line-height: 2;
}

@media (max-width: 767px) {
  .open {
    margin: 0 auto;
    line-height: 2;
    padding: 20px;
    font-size: 1.5rem;
  }
}

.navAdjust {
  padding: 10px 50px;
  box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.151);
  -webkit-box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.151);
  -moz-box-shadow: 7px 1px 15px 2px rgba(0, 0, 0, 0.151);
  background-color: #64c9cf;
}
.adjust a {
  text-decoration: none;
  padding: 10px 10px;
  color: #df711b;

  font-family: "Raleway", sans-serif;
}
.brand {
  font-family: "Roboto", sans-serif;
  text-decoration: none;
}

.adjust a::before {
  transform: scaleX(0);
  transform-origin: bottom;
}

.adjust a:hover::before {
  transform: scaleX(1);
  transform-origin: bottom;
}

.adjust a::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  inset: 0 0 0 0;
  background: hsl(200 100% 80%);
  z-index: -1;
  transition: transform 0.3s ease;
}

.adjust a {
  position: relative;
}
