.body {
  padding: 100px 10px;
  padding-bottom: 30%;
  text-align: center;
  height: 100vh;
  color: #001e6c;
}

body label {
  padding-bottom: 30px;
  font-size: 1.5rem;
}

body .input {
  width: 90%;
  height: 60px;
  color: #082032;
  display: block;
  margin: auto;
  text-align: center;
  text-transform: capitalize;
}

.button {
  margin-top: 40px;
  color: #fff;
  background-color: #0d6efd;
  border-color: #0d6efd;
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  border-radius: 0.25rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.showmore {
  list-style: none;
  padding-top: 20px;
  background-color: #ffe6e6;
  width: 350px;
  border-radius: 10px;
  box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  -webkit-box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  -moz-box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  margin: 10px auto;
  padding: 20px;
}
