.body {
  box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  -webkit-box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  -moz-box-shadow: -3px 14px 23px -2px rgba(0, 0, 0, 0.52);
  margin: 0 auto;
  padding: 10px;
  background-color: #141e61;
  color: #eeeeee;
  border-radius: 10px;
  position: relative;
  height: 300px;
  text-align: left;
}

.weatherIcon {
  font-size: 3rem;
  position: absolute;
  top: 1px;
  right: 20px;
  animation: leftToRight 3s ease-in-out infinite;
}

@keyframes leftToRight {
  0% {
    transform: translateY(0%);
  }

  25% {
    transform: translateY(10%);
  }
  50% {
    transform: translateY(0%);
  }
  75% {
    transform: translateY(10%);
  }
  100% {
    transform: translateY(0%);
  }
}
